import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';
import Popper from 'popper.js';

function GameButtons(props) { // rock, paper, and scissor buttons
	if (props.value.includes("Rock")) {
    return (
      <button class="btn btn-outline-primary mr-4" disabled={props.value.includes("true")} onClick={props.onClick}>
        {<img src = 'https://goodday451999.github.io/Rock-Paper-Scissors-Neo/images/stone.png' width="200" height="200"/>}
      </button>
    );
  } else if (props.value.includes("Paper")) {
     return (
      <button class="btn btn-outline-primary mr-4" disabled={props.value.includes("true")} onClick={props.onClick}>
        {<img src = 'https://hacktoberfest.lingonsaft.com/images/paper.png' width="200" height="200"/>}
      </button>
     );
  } else if (props.value.includes("Scissors")) {
    return (
      <button class="btn btn-outline-primary" disabled={props.value.includes("true")} onClick={props.onClick}>
        {<img src = 'https://hacktoberfest.lingonsaft.com/images/scissors.png' width="200" height="200"/>}
      </button>
    );
  }
}


class Game extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
	  playerScore: 0,
      computerScore: 0,
      results: ["n_nothing","nothing"], // last move played by each player
      currentPage : "game", // to control which page loads
	  movesIsDisabled: ["Rock", "Paper", "Scissors", "false"], // stores names for the buttons, and if the buttons are disabled
	  username: "", // registered username
	  password: "", // registered password
	  nameinput: "", // username input by user to login
	  passinput: "" // password input by user to login
    };

    this.gamePage  = this.gamePage.bind(this);
	this.loginPage  = this.loginPage.bind(this);
	this.login  = this.login.bind(this);
	this.resetGame = this.resetGame.bind(this);
	this.registerPage  = this.registerPage.bind(this);
	this.handleChange = this.handleChange.bind(this);
	this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  
  handleClick(i) { // handles the user clicking on rock, paper or scissor
	if(this.state.playerScore === 10 || this.state.computerScore === 10) { // disables buttons when a player reaches a score of 10
		  this.setState({
			movesIsDisabled: ["Rock", "Paper", "Scissors", "true"]
		  });
	} else {
	
    const moves = ["Rock", "Paper", "Scissors"];
    const move = moves[Math.floor(Math.random() * Math.floor(3))]; // pick a random move for computers turn
    const result = i + move;
    
    if (i.split("_")[1] === move) { // in case of tie only update results to the latest move by player and computer
      this.setState({results: [i,move]}) 
    }else if (result.includes('RockScissors') || result.includes('ScissorsPaper') || result.includes('PaperRock')) { // if it's the players turn
      this.setState({
        player: this.state.player+1, // increase player score if they won
        results: [i,move],
    });
    } else { // if it's the computers turn
      this.setState({
        computer: this.state.computerScore+1, // increase computer score if they won
        results: [i,move],
      });
    }
	}
  }
  
  renderButton(i) { // creates rock paper, scissor buttons
		return (
			<GameButtons
				value={i}
				onClick={() => this.handleClick(i)}
			/>
		);
	}
  
  
  loginPage() { // set current page to login page
    this.setState({
		currentPage : "login",
    });
  }
  
  gamePage() { // set current page to game page
	this.setState({
		currentPage : "game",
    });
  }
  
  registerPage() { // set current page to register page
	this.setState({
		currentPage : "register",
    });
  }
  
  checkWinner() { // check if there's a winner and print the winner
	 if(this.state.playerScore === 10) {
		return(<div class="d-flex justify-content-center text-success font-weight-bold">{"You win!"}</div>);
	} else if(this.state.computerScore === 10) {
		return(<div class="d-flex justify-content-center text-danger font-weight-bold">{"Computer wins."}</div>);
	}
  }
  
  resetGame() { // resets the game state
	  this.setState({
		  playerScore: 0,
		  computerScore: 0,
		  results: ["n_nothing","nothing"],
		movesIsDisabled: ["Rock", "Paper", "Scissors", "false"]
		  
	  });
	return(<Game />);
  }
  
  handleSubmit() { // handles user login details
	  this.setState({
		  currentPage: "login",
	  });
	 alert("Account with username \""+ this.state.username +"\" created.");
	 
  }
  
  handleChange(event) { // updates vales for username and password on register
	  const name = event.target.name;
	  const value = event.target.value;
	  
	  this.setState({
		  [name]: value
	  });
  }
  
  storeLogin(event) { // updates values for nameinput and passinput on login
	  const name = event.target.name;
	  const value = event.target.value;
	  
	  this.setState({
		  [name]: value
	  });
  }
  
  login(event) { // checks the login details match the register details
	  if (this.state.username == this.state.userinput && this.state.password == this.state.passinput) {
		  this.setState({
			currentPage: "game"
		});
	  } else {
		  alert("Username or password incorrect");
	  }
  }
  
  render() {
    const game = ( // contetns for game page
	<div class="d-flex justify-content-center">
       <div>
	   <div class="d-flex justify-content-center mt-4">
		<h2 class="text-primary">Rock Paper Scissors!</h2></div>
		<div class="d-flex justify-content-center mt-2"><h4 class="text-primary">Game Page</h4></div>
        <div>
		<div class="d-flex justify-content-center mt-4">
				  {this.renderButton(this.state.movesIsDisabled[3].concat("_",this.state.movesIsDisabled[0]))}
				  {this.renderButton(this.state.movesIsDisabled[3].concat("_",this.state.movesIsDisabled[1]))}
				  {this.renderButton(this.state.movesIsDisabled[3].concat("_",this.state.movesIsDisabled[2]))}
		</div>
          <div>
			<div class="d-flex justify-content-center mt-4">{"You played " + this.state.results[0].split("_")[1] + ". Computer played " + this.state.results[1] + "."}</div>
			<div class="d-flex justify-content-center mt-2">{"Computer score: " + this.state.computerScore}</div>
			<div class="d-flex justify-content-center mt-2">{"Player score: " + this.state.playerScore}</div>
				{this.checkWinner()}
			</div>
        </div>
        <div class="d-flex justify-content-center mt-4">
			<button class="btn btn-outline-dark" onClick={this.registerPage}>Register</button>
            <button class="btn btn-outline-dark" onClick={this.loginPage}>Login</button>
			<button class="btn btn-primary" onClick={this.resetGame}>Reset Game</button>
          </div>
      </div>
	  </div>
    );
	
	const register = ( // contents for register page
	<div>
		   <div class="d-flex justify-content-center mt-5">
		   <div>
		   <div class="d-flex justify-content-center mt-4">
				<h2 class="text-primary">Rock Paper Scissors!</h2></div>
				<div class="d-flex justify-content-center mt-2"><h4 class="text-primary">Register Page</h4></div>
			<form onSubmit={this.handleSubmit}>
			<div class="form-group mt-4">
				<label>
					Username:
					<input name="username" type="text" placeholder="username" onChange={this.handleChange} />
				</label>
			</div>
			<div class="form-group">
				<label>
					Password:
					<input name="password" type="password" placeholder="password" onChange={this.handleChange} />
				</label>
			</div>
			<div class="d-flex justify-content-center">
				<input type="submit" class="btn btn-primary" value="Register" />
				<button class="btn btn-outline-dark" onClick={this.gamePage}>Play</button>

			</div>
      </form>
	  </div>
	  </div>
		  
		  </div>
		);
		
	const login = ( // contents for login page
	<div>
		   <div class="d-flex justify-content-center mt-4">
			<div>
		   		   <div class="d-flex justify-content-center mt-4">
				   <h2 class="text-primary">Rock Paper Scissors!</h2></div>
				   <div class="d-flex justify-content-center mt-2"><h4 class="text-primary">Login Page</h4></div>

			<form onSubmit={this.login}>
			<div class="form-group mt-4">
				<label>
					Username:
					<input name="nameinput" type="text" placeholder="username" onChange={this.storeLogin} />
				</label>
			</div>
			<div class="form-group">
				<label>
					Password:
					<input name="passinput" type="password" placeholder="password" onChange={this.storeLogin} />
				</label>
			</div>
			<div class="d-flex justify-content-center">
				<input type="submit" class="btn btn-primary" value="Login" />
				<button class="btn btn-outline-dark" onClick={this.gamePage}>Play</button>
			</div>
      </form>
	  </div>
	  </div>
		  </div>
		);
  
	if(this.state.currentPage == "login") {
		return(login);
	} else if(this.state.currentPage == "register") {
		return(register);
	} else {
		return(game);
	}
  }
}



ReactDOM.render(
  <Game />,
  document.getElementById('root'),
);
